<template>
  <v-app>
  <div>
  <v-app-bar color="orange">
    <v-toolbar-title>Movie Critic</v-toolbar-title>
    <v-spacer></v-spacer>
    {{ balance }}
  </v-app-bar>
  </div>
  <v-container>
    <v-row class="text-center">
      <v-col class="ma-5 mx-auto" cols="8">
        <v-form ref="form">
          <v-row class="text-center">
            <v-col cols="6"><v-text-field v-model="title" :rules="titleRules" label="Title" required></v-text-field></v-col>
            <v-col cols="2"><v-text-field v-model="rating" :rules="ratingRules" label="Rating" type="number" min="0" max="10" required></v-text-field></v-col>
            <v-col cols="1"><v-icon large class="mt-4" @click="addReview">mdi-plus-circle-outline</v-icon></v-col>
          </v-row>
        </v-form>
      </v-col>
    </v-row>
    <v-row class="text-center">
      <v-col class="ma-5 mx-auto" cols="8">
        <v-data-table :headers="headers" :items="reviews" item-key="date" class="elevation-1">
          <template v-slot:item.date="{ item }">
            <span>{{ new Date(item.date * 1000).toISOString() }}</span>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
  </v-app>
</template>

<script>
import MovieCritic from "@/eth/movieCritic";
const movieCritic = new MovieCritic();

export default {
  name: 'App',
  data() {
    return {
        title: undefined,
        rating: undefined,
        titleRules: [
          v => !!v || 'Title is required',
        ],
        ratingRules: [
          v => !!v || 'Rating is required',
        ],
        balance: 0,
        numReviews: 0,
        reviews: [],
        headers: [
          {
            text: "Title",
            value: "title",
          },
          {
            text: "Score",
            value: "score",
          },
          {
            text: "Date",
            value: "date",
          },
        ]
    }
  },
  async mounted() {
    console.log("CONTRACT", movieCritic.contract);
    await movieCritic.init();
    console.log("CONTRACT", movieCritic.contract);
    await this.getNumReviews();
    this.getReviews();
    this.getBalance();
  },
  methods: {
      getBalance() {
        console.log("Retrieving current balance...");
        movieCritic.getBalance("ether").then((b) => {
          this.balance = b;
        });
      },
      async addReview() {
        if (!this.$refs.form.validate()) {
            return;
        }
        console.log("Adding review");
        await movieCritic.addReview(this.title, this.rating);
        this.getBalance();
        await this.getNumReviews();
        this.getReviews();
      },
      async getNumReviews() {
        console.log("Getting number of reviews...");
        this.numReviews = await movieCritic.getNumReviews();
        console.log(`Found ${this.numReviews} reviews`);
      },
      async getReview(reviewId) {
        console.log(`Getting review ${reviewId}`);
        const r = await movieCritic.getReview(reviewId);
        return r;
      },
      async getReviews() {
        console.log("Getting reviews...");
        this.reviews = [];
        for (let i = this.numReviews - 1; i >= 0; i--) {
          let r = await this.getReview(i);
          this.reviews.push(r);
        }
      },
  }
}
</script>
