import Web3 from "./web3";
import contractArtifact from "../../dapp/build/contracts/MovieCritic.json";

class Review {
    constructor(title, score, date) {
        this.title = title;
        this.score = score;
        this.date = date;
    }
}


class MovieCritic {
    constructor() {
        this.web3 = undefined;
        this.contract = undefined;
    }

    async init() {
        this.web3 = await Web3();
        const contractAbi = contractArtifact.abi;
        //const contractDeployments = Object.keys(contractArtifact.networks);
        //const contractAddress = contractArtifact.networks[contractDeployments[contractDeployments.length - 1]].address;
        //console.log(`Contract Address: ${contractAddress}`);
        this.contract = new this.web3.eth.Contract(contractAbi, "0xabF2AE6e207667B0D41F786f5537acabBb435063");
    }

    async getAccount() {
        const accounts = await this.web3.eth.getAccounts();
        const account = accounts[0];
        console.log(`Got account ${account}`)
        return account;
    }

    async getBalance(unit) {
        if (!this.web3) {
            return undefined;
        }
        let account = await this.getAccount();
        const balWei = await this.web3.eth.getBalance(account);
        //console.log(unit); // FIXME
        //return balWei;
        const balance = await this.web3.utils.fromWei(balWei, unit);
        return balance;
    }

    async addReview(name, score) {
        const account = await this.getAccount();
        await this.contract.methods.addReview(name, score).send({from: account});
    }

    async getNumReviews() {
        const account = await this.getAccount();
        let r = await this.contract.methods.getNumReviews().call({from: account});
        return r;
    }

    async getReview(reviewId) {
        const account = await this.getAccount();
        let _r = await this.contract.methods.getReview(reviewId).call({from: account});
        const r = new Review(_r[0], _r[1], _r[2]);
        return r;
    }


}

export default MovieCritic;
